<template>
  <div class="sports">
    <van-cell title="开始运动"  style=" background-color: white;"/>
    <van-circle v-model="currentRate" :rate="80" :speed="100" :text="text"  style="margin-top: 1rem;"/>
<van-steps direction="vertical" :active="1">
  <van-step>
    <h3>运动分>40</h3>
    <p>2022-10-12 12:40</p>
  </van-step>
  <van-step>
    <h3>运动分>80</h3>
    <p>2022-11-07 10:00</p>
  </van-step>
  <van-step>
    <h3>运动分>90</h3>
    <p>未知</p>
  </van-step>
</van-steps>
    <van-picker
  title="选择运动模式"
  show-toolbar
  :columns="columns"
  @cancel="onCancel"
  @confirm="onConfirm"
  v-show="picker"
/>
    <van-cell center title="定位辅助">
  <template #right-icon>
    <van-switch v-model="checked1" size="24" />
  </template>
</van-cell>
<van-cell center title="计步功能">
  <template #right-icon>
    <van-switch v-model="checked2" size="24" />
  </template>
</van-cell>
<van-cell center title="启用模式选择">
  <template #right-icon>
    <van-switch v-model="picker" size="24" />
  </template>
</van-cell>
<van-cell title="校对日期"  value="校对"  is-link @click="(showdate=true)"/>
<van-datetime-picker
  v-model="currentDate"
  type="date"
  title="选择年月日"
  :min-date="minDate"
  :max-date="maxDate"
  v-show="showdate"
  @cancel="(showdate=false)"
  @confirm="ondate"
/>


<van-cell title="基础计时器"   is-link/>
<br>
<van-count-down
  ref="countDown"
  millisecond
  :time="3000"
  :auto-start="false"
  format="ss:SSS"
  @finish="finish"
/>
<br>
<van-grid clickable  :column-num="3" icon-size="30px" square>
  <van-grid-item  icon="play-circle-o" @click="start" />
  <van-grid-item  icon="pause-circle-o" @click="pause" />
  <van-grid-item  icon="replay" @click="reset" />
</van-grid>
</div>


</template>

<script>
import { Notify } from 'vant';
import { Toast } from 'vant';
export default {
  data() {
    return {
      currentRate: 0,
      showdate:false,
      checked1:false,
      checked2:true,
      picker:false,
      columns: ['快跑', '步行', '瑜伽', '健身'],
      minDate: new Date(2020, 0, 1),
      maxDate: new Date(2025, 10, 1),
      currentDate: new Date(2022, 11, 1),
    };
  },
  methods: {
    onConfirm(value, index) {
      Toast(`当前值：${value}, id：${index}`);
      this.picker=false
    },
    // onChange(picker, value, index) {
    //   // Toast(`当前值：${value}, id：${index}`);
    // },
    onCancel() {
      Toast('取消');
    },
    ondate(){
      Notify('修改成功');
      this.showdate=false
    },
    start() {
      this.$refs.countDown.start();
    },
    pause() {
      this.$refs.countDown.pause();
    },
    reset() {
      this.$refs.countDown.reset();
    },
    finish() {
      Toast('倒计时结束');
    },
  },
  computed: {
    text() {
      return "运动分："+this.currentRate.toFixed(0);
    },
  },
};
</script>

<style>
.van-grid-item__text {
  font-size: 20px;
}
</style>